export const STREAM_STATUS = {
  INITIALIZED: 'INITIALIZED',
  ACTIVE: 'ACTIVE',
  PAUSED: 'PAUSED',
  FINISHED: 'FINISHED',
  INTERRUPTED: 'INTERRUPTED',
};
export const STREAM_DIRECTION = {
  IN: 'in',
  OUT: 'out',
};
export const STREAM_AUTODEPOSIT_STATUS = {
  ENABLED: 'enabled',
  DISABLED: 'disabled',
};

export const STREAM_ACTION_TYPE = {
  INIT: 'Init',
  DEPOSIT: 'Deposit',
  START: 'Start',
  WITHDRAW: 'Withdraw',
  PAUSE: 'Pause',
  STOP: 'Stop',
};

export function streamDirection({stream, account}) {
  if (!account) return '';

  if (stream.owner_id === account.accountId) {
    return 'out';
  } else if (stream.receiver_id === account.accountId) {
    return 'in';
  } else {
    return '';
  }
}
